/* ===============
   EVENTS BY CATEGORY WIDGET
================== */

.paragraph--type--event-by-category{

	//add a bit more space before start of view if there is a title
	h2.widget-title{
		margin-bottom: 1.5em;
	}

	.view-content{
		@include safeFlex(row,wrap,null,null);
	}

	//override normal 250px image width to allow shrinkage when in smaller spaces
	.views-row.horizontal{
		.card-image{
			@include tab{
				@include safeFlexSize(0,0,30%);
			}
		}
	}
	
}//end news by category

